#home img {
    width: 100%;
}

.typed-cursor {
    font-size: 30px;
}

.homeButton>button {
    /* animation: homeButton 3s infinite; */
    background-color: transparent;
    overflow: hidden;
    position: relative;
    transition: .2s 1s ease-in-out;
}

.homeButton>button:hover {
    background-color: transparent;
}

.homeButton>button:hover:before {
    content: "";
    animation: homeButton 1s;
    position: absolute;
    width: 100%;
    border-top: 4px solid;
    top: 0;
    left: 0;
}

.homeButton>button:hover:after {
    content: "";
    animation: homeButton 1s;
    position: absolute;
    width: 100%;
    border-bottom: 4px solid;
    bottom: 0;
    right: 0;
}

.homeButton>button:hover {
    box-shadow: inset 15px 0px 15px -15px, inset -15px 0px 15px -15px;
}

