.App {
    background-color: #27282c;
    /* background: linear-gradient(45deg, #1d3354, #1d3354); */
    background-repeat: no-repeat;
    font-family: 'Bruno Ace SC', cursive;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    color: #00bfff !important;
}

/* Project light */
.tubelightAnime {
    animation: tubelight 5s infinite;
}

/* 0%,2%,5%,10%,12%,13%,16%,18%,20%,24%,26% */
/* 1%, 3%, 6%, 8%, 11%, 15%, 19%, 22%, 25%,100% */
@keyframes tubelight {

    1%,
    21%,
    36%,
    46%,
    51%,
    56%,
    60%,
    64% {
        color: white;
        text-shadow: none;
    }

    0%,
    20%,
    35%,
    45%,
    50%,
    55%,
    59%,
    63% {
        color: #00bfff;
    }

    70%,
    100% {
        text-shadow: 0px 0px 10px #00bfff;
    }

}

/* Skills last image fix */

.skills-card:last-of-type img {
    padding: 25% 10%;
}

/* Loading animation */

#load {
    background-color: #27282c;
    height: 100vh;
    color: #00bfff;
    display: flex;
    justify-content: center;
    font-size: 30px;
    align-items: center;
}

#load>p {
    margin-top: -10vh;
    border: 2px solid;
    padding: 20px;
    opacity: 0;
    animation: welcome 3s;
}

@keyframes welcome {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

/* Glassmorphism */

#glass {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(7px);
    -webkit-backdrop-filter: blur(7px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    border-radius: 20px;
    /* box-shadow: 0 0px 15px 0 rgba(0, 0, 0, 0.37); */
}